header{
    display: grid;
    grid-template-columns: 2fr 1fr;
    background-color: black;
    padding: 20px;
    padding-left: 50px;
    padding-right: 50px;
  }
  
  .logo{
    color: white;
  }
  
  nav{
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
  }
  
  .nav_item{
    background-color: black;
    color: #001AFF;
    font-weight: bold;
    font-size: 30px;
    cursor: pointer;
    text-decoration: none;
  }
  .nav_item:hover{
    color: white;
  }
  .cart_link{
    display: grid;
    grid-template-columns: 2fr 0.5fr;
  }
  .cartItems_count{
    font-size: 14px;
    font-weight: bold;
    background-color: white;
    color: #001AFF;
    border-radius: 20px;
    padding: 5px;
    margin: auto;
  }